<template>
  <b-container>
    <div>
      <b-row class="pb-2">
        <b-col class="text-center pt-3">
          <p class="h3 text-center">    <strong class="parrafoCategorias">  Imagenes</strong></p>
        </b-col>
      </b-row>
      <ValidationObserver ref="altaImagenes">
        <b-card
          no-body
          class="mb-3 p-0"
          header="Imagen Principal (esta imagen es la que se vera en los encabezados)"
          border-variant="success"
          header-border-variant="success"
          header-bg-variant="transparent"
        >
          <transition
            v-for="(value, index) in this.imgPrimera"
            :key="index"
            name="fade"
          >
            <b-container class="pb-3">
              <b-row no-gutters>
                <b-col cols="12" sm="6" md="4" lg="2">
                  <div v-if="value.loadingImg" class="py-5 text-center"></div>
                  <b-card-img
                   thumbnail
                    fluid
                    alt="Responsive image"
                    style="max-height: 225px"
                    :src="
                      value.url == null
                        ? 'https://i.pinimg.com/originals/ad/53/64/ad53643d33b99130bc99e04d19857e39.png'
                        : value.url
                    "
                    v-else
                    class="rounded-0"
                  ></b-card-img>
                </b-col>
                <b-col cols="12" sm="6" md="8" lg="10">
                  <b-card-body class="p-3">
                    <b-card-text>
                      <b-row>
                        <b-col md="8" class="mt-2 mt-md-0">
                          <b-form-group
                            id="archivo-label"
                            label="Imagen"
                            label-for="documento"
                          >
                            <ValidationProvider
                              :name="'archivo0'"
                              :rules="'required'"
                              v-slot="{ errors, valid }"
                            >
                              <b-form-file
                                v-model="value.file"
                                size="sm"
                                accept="image/jpeg"
                                :state="
                                  errors[0]
                                    ? valid && value.file && value.url
                                      ? true
                                      : false
                                    : null
                                "
                                placeholder="Seleccione una imagen..."
                                browse-text="Examinar"
                                @change="upAws($event, value)"
                              ></b-form-file>
                              <b-form-invalid-feedback
                                v-for="error in errors"
                                :key="error.key"
                                >{{ error }}</b-form-invalid-feedback
                              >
                            </ValidationProvider>
                          </b-form-group>
                        </b-col>
                      </b-row>
                    </b-card-text>
                  </b-card-body>
                </b-col>
              </b-row>
            </b-container>
          </transition>
        </b-card>
        <b-card
          v-if="this.cantImagenesAsubir > 0"
          no-body
          class="mb-3 p-0"
          header="Imagenes Secundarias (Opcional)"
          border-variant="success"
          header-border-variant="success"
          header-bg-variant="transparent"
        >
          <transition
            v-for="(value, index) in this.imagenes"
            :key="index"
            name="fade"
          >
            <b-card no-body class="mb-3 p-0">
              <b-row no-gutters>
                <b-col cols="12" sm="6" md="4" lg="2">
                  <div v-if="value.loadingImg" class="py-5 text-center"></div>
                  <b-card-img
                   thumbnail
                    fluid
                    alt="Responsive image"
                    style="max-height: 225px"
                    :src="
                      value.url == null
                        ? 'https://i.pinimg.com/originals/ad/53/64/ad53643d33b99130bc99e04d19857e39.png'
                        : value.url
                    "
                    v-else
                    class="rounded-0"
                  ></b-card-img>
                </b-col>
                <b-col cols="12" sm="6" md="8" lg="10">
                  <b-card-body class="p-3">
                    <b-card-text>
                      <b-button
                        class="cancel-button"
                        size="sm"
                        @click="quitarImagen(value)"
                        variant="outline-secondary"
                        v-if="!value.obligatorio"
                      >
                        <b-icon icon="x-square" aria-hidden="true"></b-icon>
                      </b-button>
                      <b-row>
                        <b-col md="8" class="mt-2 mt-md-0">
                          <b-form-group
                            id="archivo-label"
                            label="Imagen"
                            label-for="documento"
                          >
                            <ValidationProvider
                              :name="'archivo' + index"
                              :rules="'required'"
                              v-slot="{ errors, valid }"
                            >
                              <b-form-file
                                v-model="value.file"
                                size="sm"
                                accept="image/jpeg"
                                :state="
                                  errors[0]
                                    ? valid && value.file && value.url
                                      ? true
                                      : false
                                    : null
                                "
                                placeholder="Seleccione una imagen..."
                                browse-text="Examinar"
                                @change="upAws($event, value)"
                              ></b-form-file>
                              <b-form-invalid-feedback
                                v-for="error in errors"
                                :key="error.key"
                                >{{ error }}</b-form-invalid-feedback
                              >
                            </ValidationProvider>
                          </b-form-group>
                        </b-col>
                      </b-row>
                    </b-card-text>
                  </b-card-body>
                </b-col>
              </b-row>
            </b-card>
          </transition>
        </b-card>
      </ValidationObserver>
      <b-col class="text-center">
        <b-button
          pill
          left
          v-if="this.cantImagenesAsubir <= 3"
          @click="agregarImagen"
          variant="primary"
          >Nueva
        </b-button>
      </b-col>
    </div>
  </b-container>
</template>
<script>
export default {
  name: "ImagenesCarga",
  props: {
    imagenes: {
      type: Array,
      default: () => ({
        id: 0,
        url:
          "https://i.pinimg.com/originals/ad/53/64/ad53643d33b99130bc99e04d19857e39.png",
        file: null,
        tipo: null,
        loadingImg: false,
        estado: 1,
        key: 0,
        obligatorio: true,
        base64: "",
        primera: false,
      }),
    },
    imgPrimera: {
      type: Array,
      default: () => ({
        id: 0,
        url:
          "https://i.pinimg.com/originals/ad/53/64/ad53643d33b99130bc99e04d19857e39.png",
        file: null,
        tipo: null,
        loadingImg: false,
        estado: 1,
        key: 0,
        obligatorio: true,
        base64: "",
        primera: true,
      }),
    },
  },
  data() {
    return {
      url: null,
      loading: false,
      loadingImg: false,
      cantImagenesAsubir: 0,
    };
  },
  computed: {},
  created() {
   
  },

  methods: {
    upAws(event, img) {
      const file = event.target.files[0];
      img.url = URL.createObjectURL(file);
      this.createBase64Image(file, img);
    },
    createBase64Image(file, img) {
      const reader = new FileReader();
      reader.onload = (e) => {
        img.base64 = e.target.result;
      };

      reader.readAsDataURL(file);
    },
    close() {
      this.$emit("cancelar", false);
    },
    agregarImagen() {
      this.cantImagenesAsubir++;
      this.imagenes.push({
        borrado: 0,
        id: 0,
        estado: 1,
        loadingImg: false,
        url: null,
        file: null,
        obligatorio: false,
      });
    },
    async validate() {
      if (!this.loadingImg && !this.loading && !this.base64) {
        let result = await this.$refs.altaImagenes.validate();
              if (result == true) {
                  if (! this.sonImagenesValidas(this.imagenes) || !this.sonImagenesValidas(this.imgPrimera)){
                        alert("Atencion: se permiten solo fotos JPG")
                        return false;
                  }
              }
        return result;
      } else {
        return false;
      }
    },
    sonImagenesValidas(imagenes) {
      let validas = true;
      imagenes.forEach((img) => {
        if (img.file.type != "image/jpeg") {
          validas = false;
          return false;
        }
      });
      return validas;
    },

    quitarImagen(imagen) {
      let confirmacion = confirm(
        "¿Esta seguro que desea eliminar esta imagen?"
      );
      if (confirmacion) {
        let index = this.imagenes.indexOf(imagen);
        if (index > -1) {
          this.imagenes.splice(index, 1);
          this.cantImagenesAsubir--;
        }
      } else if (confirmacion) {
        imagen.estado = 0;
      }
    },
  },
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>